  }
  
  .tutor-list-wrap .tutor-list-table tbody tr:not(:last-child) {
	  border-bottom: 1px solid #dcdce1;
  }
  
  .tutor-list-footer {
	  display: flex;
	  justify-content: space-between;
	  align-items: center;
	  border-top: 1px solid #dcdce1;
  }
  
  .tutor-list-footer .tutor-report-count {
	  font-size: 14px;
	  line-height: 1.71;
	  color: #737787;
  }
  
  .tutor-list-footer .tutor-report-count strong {
	  color: #3f435b;
  }
  
  .tutor-list-footer .page-numbers.current {
	  border-color: #3e64de;
	  background: transparent;
  }
  
  .tutor-list-footer .page-numbers {
	  border-radius: 4px;
	  border: solid 1px #dcdce1;
	  padding: 11px 14px;
	  font-size: 14px;
	  font-weight: 400;
	  color: #737787;
	  background: #fff;
	  outline: none;
	  transition: 0.2s;
