	  font-weight: 500;
	  line-height: 1.75;
	  text-align: center;
	  color: #737787;
	  border-radius: 3px;
	  border: solid 1px #dcdce1;
	  padding: 5px 11px;
	  margin-right: 15px;
	  text-decoration: none;
