  .tutor-list-table td .details-button {
	  text-align: right;
	  display: flex;
	  align-items: center;
  }
  
  .tutor-list-table .course-link a {
	  color: inherit;
	  text-decoration: none;
  }
  .tutor-list-table .tutor-icon-detail-link,
  .profile-table .tutor-icon-detail-link {
	  font-size: 14px;
  }
  
